.advantages {
  padding-top: 42px;
  padding-bottom: 60px;
  background-color: var(--primary-bg-color);

}

.advantages__list {
  display: block;
  @include tablet {
     display: flex;
     align-items: center;
     justify-content: baseline;

    }
}

.advantages__item {
  width: 100%;

  @include tablet {
  width: calc((100% - 160px)/3);
}

  @include desktop {
    width: calc((100% - 64px)/3);
}
}


.advantages__item:not(:last-child) {
  margin-bottom: 25px;
  @include tablet {
      margin-right: 80px;
      margin-bottom: 0;
    }
        @include desktop {
          margin-right: 32px;
        }
}

.advantages__title {
  font-family: 'Roboto';
  font-weight: 700;
  font-size: 36px;
  line-height: 1.17;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--secondary-text-color);
  margin-bottom: 18px;

  @include tablet {
      font-size: 48px;
      line-height: 1.22;
    }
  @include desktop {
      font-size: 58px;
    }
}

.advantages__text {
  font-family: 'Montserrat';
  font-weight: 700;
  font-size: 12px;
  line-height: 1.17;
  text-align: center;
  color: var(--secondary-text-color);
  @include tablet {
      font-size: 14px;
      line-height: 1.71;
    }
    @include desktop {
      font-size: 16px;
      line-height: 1.75;
      padding-left: 32px;
      padding-right: 32px;
    }
}

.advantages__wrap {
  margin-bottom: 17px;
  background-color: var(--accept-color);
  border-radius: 25px;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: auto;
  margin-left: auto;
}
